import uQRCode from '@/common/SDK/uqrcode.js'

export default {
	data() {
		return {
			tis:"保存到相册",
			showBtn:false
		};
	},
	onLoad() {



		this.make();
	},
	methods:{
		// 二维码
		    make() {
				console.log(uQRCode)
		      uQRCode.make({
		        canvasId: 'qrcode',
		        componentInstance: this,
		        text: 'uQRCode',
		        size: 215,
		        margin: 10,
		        backgroundColor: '#ffffff',
		        foregroundColor: '#000000',
		        fileType: 'jpg',
		        // correctLevel: uQRCode.defaults.correctLevel,
		        success: res => {
		          console.log(res)
		        }
		      })
		    },
		// 截图，调用webview、Bitmap方法
		printscreen(){
			this.tis = "正在保存"
			let ws=this.$mp.page.$getAppWebview();
			let bitmap = new plus.nativeObj.Bitmap();
			this.showBtn = false;
			this.$nextTick(function(){
				setTimeout(()=>{
					ws.draw(bitmap,(e)=>{
						this.showBtn = true;
						console.log('bitmap绘制图片成功');
						console.log("e: " + JSON.stringify(e));
						bitmap.save("_doc/Qr.jpg", {
							overwrite: true,
							quality: 100
						}, (i)=>{
							plus.gallery.save(i.target,(e)=>{
								uni.showToast({
									title:'保存成功'
								})
								this.tis = "保存到相册"
								bitmap.clear(); //销毁
							},(e)=>{
								bitmap.clear(); //销毁
							});
						},(e)=>{
							console.log('保存图片失败：' + JSON.stringify(e));
						});
					},(e)=>{
						console.log('bitmap绘制图片失败：'+JSON.stringify(e));
					});
				},200)
			})
		}
	}
}
